<template>
    <div>
        {{ show_first }}
    </div>
</template>

<script>
    export default {
        props: {
            _item: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        computed: {
            show_first() {
                let res = '否'
                this._item.buys.map(e => {
                    if (e.first_buy) {
                        res = '是'
                    }
                })
                return res
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


